import React from 'react'
require('styles/Graphic.scss')

class Graphic extends React.Component {
	constructor(props) {
	    super(props);
	    
	}
	render () {

		let title = ''

		let count = 82

		let list = []

		let datas = [
			{title: '',des: '20111390',src: '',id: 12,page: 73,views: 597},
			{title: '',des: ' Ruby Barnhill  Mark Rylance ',src: '',id: 12,page: 73,views: 597},
			{title: '2012',des: ' John Cusack  Woody Harrelson )',src: '',id: 12,page: 73,views: 597}
		]

		datas.forEach((value,key) => {
			list.push(
				<li className="graphic-item" key={key}>
					<h5 className="graphic-item-title">{value.title}</h5>
					<div className="graphic-item-content">
						<img className="graphic-item-img" src={value.src}/>
						<div className="graphic-item-master"></div>
						<p className="graphic-item-des">
							<a>{value.des}</a>
						</p>
					</div>
				</li>
			)
		})

		return (
			<div className="graphic-box">
				<div className="graphic-header">
					<h3	className="graphic-title">
						{title}
					</h3>
					<p className="graphic-notice">
						
					</p>
					<a className="graphic-more"></a>
					<a className="graphic-count">{count}{title}</a>
				</div>
				<div className="graphic-content">
					<ul className="graphic-list">
						{list}
					</ul>
				</div>
			</div>
		)
	}

}

Graphic.defaultProps = {

}

module.exports = Graphic


