import React from 'react'
require('styles/Script.scss')

class Script extends React.Component {
	constructor(props) {
	    super(props);
	    
	}
	render () {

		let title = ''

		let count = 5261

		let list = []

		let datas = [
			{title: '',sort: '',views: 15},
			{title: '',sort: '',views: 1123},
			{title: '',sort: '',views: 8},
			{title: '',sort: '',views: 4151},
			{title: '',sort: '',views: 321},
			{title: '',sort: '',views: 41},
			{title: '',sort: '',views: 9102},
			{title: '',sort: '',views: 5121},
			{title: '',sort: '',views: 2055}
		]

		datas.forEach((value,key) => {
			list.push(
				<li className="script-item" key={key}>
					<img className="script-item-img" src={value.src}/>
					<div className="script-item-content">
						<p className="script-item-title">{value.title}</p>
						<p className="script-item-info">
							<span className="script-item-sort">{value.sort}</span>
							<span className="script-item-views">{value.views}</span>
						</p>
					</div>
				</li>
			)
		})

		return (
			<div className="script-box">
				<div className="script-header">
					<h3	className="script-title">
						{title}
					</h3>
					<a className="script-more"></a>
					<a className="script-count">{count}{title}</a>
				</div>
				<div className="script-content">
					<ul className="script-list">
						{list}
					</ul>
					<div className="script-wall">
						<div className="script-wall-header">
							<strong className="script-wall-title"></strong>
						</div>
					</div>
				</div>
			</div>
		)
	}

}

Script.defaultProps = {

}

module.exports = Script


